.student
{
    color: #fff;
    display: flex;
    padding-left: 3px;
    justify-content: space-between;
    width: 400px;
    margin-bottom: 10px;
    margin-right: 5px;
    box-shadow: rgba(252, 252, 252, 0.05) 0px 6px 24px 0px, rgba(250, 250, 250, 0.08) 0px 0px 0px 1px;
}

student-info
{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.info 
{
    padding: 0px 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-sizing: border-box;
}
.student-login
{
    font-size: 25px;
    margin-top: 10px;
    margin-left: 0px;
    margin-bottom: 0;
}
.student-points
{
    margin-top: 0;
    font-weight: 100;
    text-align: center;
}
.student-Coalition
{
    margin-bottom: 10px;
    margin-top: 1px;
    margin-left: 0px;
    font-weight: 10;
}

.student img
{
    height: 80px;
    width: 80px;
    display: block;
    border-radius: 100%;
    display: block;
    margin-left: 20px;
    margin-top: 4px;
}

.student-ranking
{
    margin-right: 10px;
}

.student-ranking > h1
{
    border: 3px solid #45C387;
    color: #45C387;
    border-radius: 100%;
    font-size: 30px;
    width: 25px;
    height: 25px;
    line-height: 25px;
    text-align: center;
    padding: 10px;
}