@font-face {
  font-family: MondayFont;
  src: url(fonts/Mark\ Simonson\ \ Proxima\ Nova\ Condensed\ Black\ TheFontsMaster.com.otf)
}

body {
  margin: 0;
  font-family: MondayFont, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  box-sizing: border-box;
  background-color: #1A191E;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: MondayFont, source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
